.Footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #eee;
  padding: 3rem 0;

  &-Icons {
    display: flex;
  }

  &-Name {
    font-size: 1.8rem;
    font-weight: 700;
    margin-top: 2rem;
  }

  &-Info {
    margin-top: 1rem;
    color: #333;

    & a {
      text-decoration: none;
      color: inherit;

      &:hover {
        color: #035397;
      }
    }
  }
}
